<template>
    <div class="search"> 
        <p>找到{{index}}个结果</p>
        <div class="main">
            <div v-for="item in arr" :key="item.id">
                <img :src="item.img" alt="">
                <p>{{item.nickname}}</p>
                <p>{{item.name}}</p>
                <p>￥{{item.money}}</p>
                <p>
                    <button @click="num--">-</button>
                    <input v-model="num" disabled="disabled"/>
                    <button @click="num++">+</button>
                    <span  @click="submit(item)">添加到购物车<i class="el-icon-shopping-cart-2"></i></span>
                </p>
            </div>
        </div>
        
    </div>
</template>
<script>
export default {
    // props获取app页面数据,然后请求数据
    props:['data','arr','index'],
    data(){
        return{
            num:1,
        }
    },
    watch:{
        num:function(){
            if(this.num<=0){
                this.num=1
            }
        }
    },
    methods:{
          submit(item){
            item.count=this.num;
            this.num=1;
            this.$emit('chuanzhi',item)
        }
    }
}
</script>
<style lang="less" scoped>
.search{
    width: 60%;
    margin: 0 auto;
    margin-top: 50px;
    p{
        font-size: 24px;
        margin-bottom: 50px;
    }
   .main{
       display: flex;
       flex-wrap: wrap;
       justify-content: space-evenly;
       div{
           width: 30%;
           margin-bottom: 10vh;
           img{
               width: 85%;
           }
           p{
               width: 85%;
               margin: 0 ;
            //    text-align: center;
           }
           p:nth-child(2){
               font-size: 1.3vw;
               text-align: center;
               font-weight: bold;
               margin-top: 2vh;
           }
           p:nth-child(3){
               font-size: 1vw;
               text-align: center;
               margin-top: 1vh;
           }
           p:nth-child(4){
               text-align: center;
               color: #ffa6a8;
           }
           p:nth-child(5){
               button{
                       width: 1.5vw;
                       height: 3.1vh;
                       background: white;
                       border: none;
                       border: 1px solid black;
                       margin: 0;
                       &:hover{
                           background: #333333;
                           color: white ;
                       }
                   }
                   input{
                       vertical-align: bottom;
                       display:inline-block;
                       width: 1.5vw;
                       height: 2.9vh;
                       border: 1px solid black;
                       border-left: none;
                       border-right: none;
                       text-align: center;
                   }
                   span{
                       margin-left: 20px;
                       font-size: 1vw;
                       width: 7vw;
                       height: 3vh;
                       display: inline-block;
                       border: 1px solid black;
                       border-right:none ;
                       vertical-align: bottom;
                       text-align: center;
                       position: relative;
                       line-height: 3vh;
                       i{
                           position: absolute;
                           left: 7.5vw;
                           top: 0.2vh;
                           color: white;
                            z-index: 10;
                            font-size: 1.4vw;
                       }
                   }
                   span::after{
                       content: "";
                       position: absolute;
                       display: block;
                       width: 2.5vw;
                       height: 3.2vh;
                       background: #999999;
                       top: -1px;
                       left: 129px;
                   }
                   span:hover{
                       background: #000;
                       color: white;
                       cursor: pointer;
                   }
                   
           }
       }
   }
}
</style>
